import React, { Component, useState } from "react";
import {
  TextField,
  Snackbar,
  Button,
  IconButton,
  Select,
  InputLabel,
  FormControl,
  MenuItem,
  FormControlLabel,
  FormGroup,
  Checkbox,
} from "@material-ui/core";
import MuiAlert from "@material-ui/lab/Alert";
import Nav from "./components/Nav";
import Header from "./components/Header";
import FormsApi from "../../api/forms";
import UsersApi from "../../api/users";
import Autocomplete from "@material-ui/lab/Autocomplete";

//print
import ReactToPrint from "react-to-print";
import Print from "../../components/print";

import "../../design/main.css";
import "../../design/forms.css";
import user from "../../app_config";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

class NewSale extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      message: "Please Wait...",
      messageState: "",
      _content: {},
      form_visible: false,
      active_product_qty: 0,
      over_qty_error: false,
      active_sale_type: "retail",
      active_selling_unit: "",
      active_selling_price: "",
      products: [],
      formData: [],
      total: 0,
      discount: 0,
      batch_index: 1,
    };
  }

  handleSale = async (e) => {
    e.preventDefault();
    console.log(this.state.batch_index);
    this.setState({ ...this.state, open: true, messageState: "info" });
    const fd = new FormData(e.target);
    let content = {};
    fd.forEach((value, key) => {
      content[key] = value;
    });
    await this.setState({ ...this.state, _content: content });

    if (this.state.formData.length !== 0) {
      this.setState({
        ...this.state,
        _content: {
          ...this.state._content,
          products_sold: this.state.formData,
          date: Date.now(),
          user: user.user.username,
        },
      });
    }

    let api = new FormsApi();
    let res = await api.post("/user/sale/new_sale", this.state._content);
    if (res.status === true) {
      this.setState({
        ...this.state,
        open: true,
        message: res.data,
        messageState: "success",
      });
      setTimeout(() => {
        window.location.reload();
      }, 200);
    } else {
      this.setState({
        ...this.state,
        open: true,
        message: res.data,
        messageState: "error",
      });
    }
  };

  handleSubmit = async (e) => {
    e.preventDefault();
    if (this.state.over_qty_error) {
      this.setState({
        ...this.state,
        open: true,
        messageState: "error",
        message: "Quantity Exceeds Available",
      });
      return;
    }
    if (!this.state.active_drug) {
      this.setState({
        ...this.state,
        open: true,
        messageState: "error",
        message: "No Selling Unit",
      });
      return;
    }
    this.setState({ ...this.state, open: true, messageState: "info" });
    const fd = new FormData(e.target);
    let _fcontent = {};
    fd.forEach((value, key) => {
      _fcontent[key] = value;
    });
    _fcontent["batch"] = this.state.batch ? this.state.batch : [];
    const product_name = this.state.formData.find(
      (e) => e.product_name === _fcontent.product_name
    );
    if (!product_name) {
      this.setState({
        ...this.state,
        open: true,
        message: "Product Added",
        messageState: "success",
        formData: [...this.state.formData, _fcontent],
        print: true,
        active_drug: null,
      });
    } else {
      this.setState({
        ...this.state,
        open: true,
        message: "Product Exists",
        messageState: "warning",
      });
    }
  };

  handleDrugNameKeyUp = async (e, v) => {
    const res = e.target.value
      ? (await UsersApi.data(`/user/sale/products/${e.target.value}`)) || []
      : [];
    if (res) {
      this.setState({ ...this.state, products: res === "Error" ? [] : res });
    }
  };
  IsJsonString(str) {
    try {
      JSON.parse(str);
    } catch (e) {
      return false;
    }
    return true;
  }

  handleChangeDrugName = (e, v) => {
    if (v) {
      if (!this.IsJsonString(v.product_units)) {
        this.setState({
          ...this.state,
          open: true,
          message: "This Product has no Selling Units, Edit It to make a sale",
          messageState: "warning",
        });
        return;
      }
      this.setState(
        {
          ...this.state,
          active_drug: v,
        },
        () => {
          this.setState(
            {
              ...this.state,
              active_selling_unit: JSON.parse(
                this.state.active_drug.product_units
              )[0].selling_unit,
              active_selling_price: JSON.parse(
                this.state.active_drug.product_units
              )[0][this.state.active_sale_type],
            },
            () => {
              this.setState({
                ...this.state,
                active_product_qty:
                  this.state.active_selling_unit ===
                  JSON.parse(this.state.active_drug.product_units)[0]
                    .selling_unit
                    ? parseInt(this.state.active_drug.product_qty)
                    : parseInt(this.state.active_drug.product_qty) /
                      parseInt(
                        JSON.parse(this.state.active_drug.product_units).find(
                          (el) => el.selling_unit === e.target.value
                        )["qty"]
                      ),
              });
            }
          );
        }
      );
    }
  };

  getTotals() {
    let total = 0;
    if (this.state.formData.length !== 0) {
      this.state.formData.forEach((e) => {
        total += parseInt(e.product_price) * parseInt(e.qty);
      });
    }
    return total;
  }

  closePopUp = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    this.setState({ ...this.state, open: false, message: "" });
  };

  render() {
    return (
      <>
        <Snackbar
          anchorOrigin={{
            vertical: "top",
            horizontal: "center",
          }}
          open={this.state.open}
          autoHideDuration={5000}
          onClose={this.closePopUp}
          action={
            <React.Fragment>
              <IconButton
                size="small"
                aria-label="close"
                color="inherit"
                onClick={this.closePopUp}
              >
                <i className="las la-times"></i>
              </IconButton>
            </React.Fragment>
          }
        >
          <Alert onClose={this.closePopUp} severity={this.state.messageState}>
            {this.state.message}
          </Alert>
        </Snackbar>
        <input type="checkbox" id="nav-toggle" defaultChecked />
        <Nav active="sale" />
        <div className="main-content">
          <Header />
          <main>
            <div className="recent-grid">
              <div className="card">
                <div className="card-header">
                  <h3>Sale List</h3>
                  <Button
                    aria-describedby={this.id}
                    variant="contained"
                    color="primary"
                    onClick={() => {
                      window.location.reload();
                    }}
                    style={{ fontSize: "17.5px", marginRight: "15px" }}
                  >
                    <span style={{ fontSize: "17.5px", marginRight: "10px" }}>
                      <i className="las la-redo"></i>
                    </span>
                    Cancel
                  </Button>
                </div>
                <div className="card-body">
                  <table width="100%">
                    <thead>
                      <tr>
                        <td>Name</td>
                        <td>Qty</td>
                        <td>Unit Price(Shs)</td>
                        <td>Total(Shs)</td>
                        <td></td>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.formData.length === 0 ? (
                        <tr>
                          <td>No Product Added</td>
                        </tr>
                      ) : (
                        this.state.formData.map((v, i) => {
                          return (
                            <tr key={i}>
                              <td className="name_cell">{v.product_name}</td>
                              <td>{v.qty}</td>
                              <td>{v.product_price}</td>
                              <td>
                                {parseInt(v.product_price) * parseInt(v.qty)}
                              </td>
                              <td>
                                <Button
                                  variant="contained"
                                  color="primary"
                                  onClick={() => {
                                    let arr = this.state.formData;
                                    arr.splice(i, 1);
                                    this.setState({
                                      ...this.state,
                                      formData: arr,
                                    });
                                  }}
                                >
                                  Remove
                                </Button>
                              </td>
                            </tr>
                          );
                        })
                      )}
                    </tbody>
                    <thead>
                      <tr>
                        <td></td>
                        <td></td>
                        <td>Total</td>
                        <td>{this.getTotals()}</td>
                      </tr>
                    </thead>
                  </table>
                </div>
              </div>
              <div className="projects">
                <form
                  className="card"
                  autoComplete="off"
                  onSubmit={this.handleSubmit}
                >
                  <div className="card-header card-header-payments">
                    <div>
                      <FormControl
                        variant="outlined"
                        label="selling_unit"
                        style={{
                          width: "85%",
                          marginInline: "20px",
                        }}
                      >
                        <InputLabel id="sale_type">Sale</InputLabel>
                        <Select
                          inputProps={{
                            name: "sale_type",
                          }}
                          label="Sale"
                          id="select_sale_type"
                          value={this.state.active_sale_type}
                          onChange={(e, v) => {
                            this.setState({
                              ...this.state,
                              active_sale_type: e.target.value,
                              active_drug: null,
                            });
                          }}
                        >
                          <MenuItem value="retail">Retail</MenuItem>
                          <MenuItem value="wholesale">Wholesale</MenuItem>
                        </Select>
                      </FormControl>
                    </div>
                    <div className="">
                      <Button
                        type="submit"
                        aria-describedby={this.id}
                        variant="contained"
                        color="primary"
                      >
                        <span
                          style={{ fontSize: "17.5px", marginRight: "10px" }}
                        >
                          <i className="las la-plus-circle"></i>
                        </span>
                        Add
                      </Button>
                    </div>
                  </div>
                  <div className="card-body">
                    <div>
                      <div className="inputCtr">
                        <h4>Product Details</h4>
                        <div className="inputs_ctr">
                          <div className="inpts_on_left">
                            <Autocomplete
                              id="combo-box-demo"
                              options={this.state.products}
                              getOptionLabel={(option) =>
                                `${option.product_generic_name} ${option.product_description_name}`
                              }
                              onChange={this.handleChangeDrugName}
                              onKeyUp={this.handleDrugNameKeyUp}
                              style={{
                                width: "85%",
                                margin: "20px",
                              }}
                              renderInput={(params) => (
                                <TextField
                                  {...params}
                                  label="Search Product"
                                  name="product_name"
                                  variant="outlined"
                                />
                              )}
                            />
                            <TextField
                              disabled={this.state.active_drug ? false : true}
                              type="number"
                              name="qty_available"
                              variant="outlined"
                              label="Available"
                              value={this.state.active_product_qty}
                              style={{
                                width: "85%",
                                margin: "20px",
                              }}
                            />
                          </div>
                          <div className="inpts_on_right">
                            <FormControl
                              variant="outlined"
                              label="selling_unit"
                              disabled={this.state.active_drug ? false : true}
                              style={{
                                width: "85%",
                                margin: "20px",
                              }}
                            >
                              <InputLabel id="selling_unit">
                                Selling Unit
                              </InputLabel>
                              <Select
                                inputProps={{
                                  name: "selling_unit",
                                }}
                                label="Selling Unit"
                                id="select_selling_unit"
                                value={this.state.active_selling_unit}
                                onChange={async (e, v) => {
                                  await this.setState({
                                    ...this.state,
                                    active_selling_price: JSON.parse(
                                      this.state.active_drug.product_units
                                    ).find(
                                      (el) => el.selling_unit === e.target.value
                                    )[this.state.active_sale_type],
                                  });
                                  this.setState(
                                    {
                                      ...this.state,
                                      active_selling_unit: e.target.value,
                                    },
                                    () => {
                                      this.setState({
                                        ...this.state,
                                        active_product_qty:
                                          this.state.active_selling_unit ===
                                          JSON.parse(
                                            this.state.active_drug.product_units
                                          )[0].selling_unit
                                            ? parseInt(
                                                this.state.active_drug
                                                  .product_qty
                                              )
                                            : parseInt(
                                                this.state.active_drug
                                                  .product_qty
                                              ) /
                                              parseInt(
                                                JSON.parse(
                                                  this.state.active_drug
                                                    .product_units
                                                ).find(
                                                  (el) =>
                                                    el.selling_unit ===
                                                    e.target.value
                                                )["qty"]
                                              ),
                                      });
                                    }
                                  );
                                }}
                              >
                                {this.state.active_drug ? (
                                  JSON.parse(
                                    this.state.active_drug.product_units
                                  ).map((v, i) => {
                                    return (
                                      <MenuItem key={i} value={v.selling_unit}>
                                        {v.selling_unit}
                                      </MenuItem>
                                    );
                                  })
                                ) : (
                                  <MenuItem value="">No Selling Units</MenuItem>
                                )}
                              </Select>
                            </FormControl>
                            <TextField
                              disabled={this.state.active_drug ? false : true}
                              error={this.state.over_qty_error}
                              type="number"
                              name="qty"
                              variant="outlined"
                              label="Quantity"
                              helperText={
                                this.state.over_qty_error
                                  ? "Quantity Exceeds Available"
                                  : ""
                              }
                              defaultValue={1}
                              onChange={async (e) => {
                                if (
                                  parseInt(e.target.value) >
                                  this.state.active_product_qty
                                ) {
                                  this.setState({
                                    ...this.state,
                                    over_qty_error: true,
                                  });
                                } else {
                                  const res = await UsersApi.data(
                                    `/user/sale/batch/${
                                      this.state.active_drug.product_id
                                    }/${1}`
                                  );
                                  if (res !== "Error" && res.length !== 0) {
                                    await this.setState(
                                      {
                                        ...this.state,
                                        batch_qty: parseInt(res[0].sum),
                                      },
                                      async () => {
                                        if (
                                          parseInt(e.target.value) >=
                                          this.state.batch_qty
                                        ) {
                                          const res = await UsersApi.data(
                                            `/user/sale/batch/${
                                              this.state.active_drug.product_id
                                            }/${this.state.batch_index + 1}`
                                          );
                                          if (
                                            res !== "Error" &&
                                            res.length !== 0
                                          ) {
                                            this.setState({
                                              ...this.state,
                                              batch_index:
                                                this.state.batch_index + 1,
                                              batch_qty: parseInt(
                                                res[res.length - 1].sum
                                              ),
                                            });
                                          }
                                        }
                                      }
                                    );
                                  }

                                  this.setState({
                                    ...this.state,
                                    over_qty_error: false,
                                  });
                                }
                              }}
                              style={{
                                width: "85%",
                                margin: "20px",
                              }}
                            />
                            <input
                              hidden
                              type="text"
                              name="product_id"
                              defaultValue={
                                this.state.active_drug
                                  ? this.state.active_drug.product_id
                                  : ""
                              }
                            />
                            <input
                              hidden
                              type="text"
                              name="product_price"
                              defaultValue={this.state.active_selling_price}
                            />
                            <input
                              hidden
                              type="text"
                              name="product_batch_index"
                              value={this.state.batch_index}
                              readOnly
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </form>
                <form
                  className="card"
                  style={{ marginTop: "20px" }}
                  onSubmit={this.handleSale}
                >
                  <div className="card-header card-header-payments">
                    <h3 className="class_payment_header">Payment</h3>
                    <div className="">
                      <ReactToPrint
                        trigger={() => {
                          return (
                            <Button
                              variant="contained"
                              color="primary"
                              style={{ marginRight: 10 }}
                            >
                              <span
                                style={{
                                  fontSize: "17.5px",
                                  marginRight: "10px",
                                }}
                              >
                                <i className="las la-print"></i>
                              </span>
                              Print
                            </Button>
                          );
                        }}
                        content={() => this.componentRef}
                      />
                      <Button type="submit" variant="contained" color="primary">
                        Finish Sale
                        <span style={{ fontSize: "15px", marginLeft: "10px" }}>
                          <i className="las la-angle-double-right"></i>
                        </span>
                      </Button>
                    </div>
                  </div>
                  <Finish t={this.getTotals()} />
                </form>
              </div>
            </div>
          </main>
        </div>
        <div style={{ display: "none" }}>
          {this.state.print ? (
            <Print
              ref={(el) => (this.componentRef = el)}
              data={this.state.formData}
            />
          ) : (
            <></>
          )}
        </div>
      </>
    );
  }
}

export default NewSale;

function Finish({ t }) {
  const [discount, setDiscount] = useState(0);
  return (
    <div className="_finish_purchase_ctr">
      <TextField
        name="total_amount"
        variant="outlined"
        label="Total"
        value={t}
        style={{
          width: "75%",
          margin: "20px",
        }}
      />
      <TextField
        name="discount"
        variant="outlined"
        label="Discount"
        type="number"
        onChange={(e) => {
          setDiscount(parseInt(e.target.value) || 0);
        }}
        style={{
          width: "75%",
          margin: "20px",
        }}
      />
      <TextField
        name="pay_amount"
        variant="outlined"
        label="Amount to Be Paid"
        value={t - discount}
        style={{
          width: "75%",
          margin: "20px",
        }}
      />
      <FormControl
        style={{
          width: "75%",
          marginLeft: "20px",
          marginBottom: "50px",
        }}
      >
        <FormGroup>
          <FormControlLabel
            control={<Checkbox defaultChecked name="print_receipt" />}
            label="Print Receipt"
          />
        </FormGroup>
      </FormControl>
    </div>
  );
}
